.section-title {
	margin-bottom: 32px;
}

#overview,
#register {
	padding-top: 14rem;
	padding-bottom: 14rem;
}

#detail,
#video,
#speakers,
#program,
#sponsors,
#contact,
footer {
	padding-top: 10rem;
	padding-bottom: 10rem;
}

#detail,
#speakers,
#sponsors,
footer {
	text-align: center;
}

#overview h3,
#detail h3,
#video h3,
#register h3,
#venue h3,
#contact h3 {
	padding-bottom: 12px;
}

#intro .btn,
#contact .btn {
	background: transparent;
	border: 2px solid #ffffff;
	border-radius: 0px;
	color: #ffffff;
	font-weight: 600;
	letter-spacing: 1px;
	font-size: 12px;
	padding: 14px 42px;
	margin-top: 42px;
	margin-right: 12px;
	-webkit-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
}

#intro .btn-danger,
#contact .btn-danger {
	background: #f2545f;
	border-color: transparent;
}

#contact .btn {
	border-radius: 100px;
}

#intro .btn-danger:hover {
	background: transparent;
	border-color: #ffffff;
	color: #ffffff;
}

#contact .btn-danger:hover {
	background: #222;
}

#intro .btn-default:hover {
	background: #ffffff;
	color: #333333;
	border-color: transparent;
}

.parallax-section {
	background-attachment: fixed !important;
	background-size: cover !important;
	background-repeat: no-repeat !important;
}

/*---------------------------------------
    Preloader section              
-----------------------------------------*/
.preloader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 99999;
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;
	background: none repeat scroll 0 0 #ffffff;
}

.sk-rotating-plane {
	width: 50px;
	height: 50px;
	background-color: #222;
	-webkit-animation: sk-rotatePlane 1.2s infinite ease-in-out;
	animation: sk-rotatePlane 1.2s infinite ease-in-out;
}

@-webkit-keyframes sk-rotatePlane {
	0% {
		-webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg);
		transform: perspective(120px) rotateX(0deg) rotateY(0deg);
	}
	50% {
		-webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
		transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
	}
	100% {
		-webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
		transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
	}
}

@keyframes sk-rotatePlane {
	0% {
		-webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg);
		transform: perspective(120px) rotateX(0deg) rotateY(0deg);
	}
	50% {
		-webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
		transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
	}
	100% {
		-webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
		transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
	}
}

/*---------------------------------------
    Navigation Links section              
-----------------------------------------*/
.custom-navbar {
	margin-bottom: 0;
	background-color: #101010;
}

.custom-navbar .navbar-brand {
	color: #ffffff;
	font-weight: 600;
	font-size: 3rem;
	line-height: 40px;
}

.custom-navbar .nav li a {
	color: #ddd;
	font-size: 12px;
	font-weight: 500;
	letter-spacing: 0.6px;
	text-transform: uppercase;
	line-height: 40px;
	transition: all 0.4s ease-in-out;
}

.custom-navbar .nav li a:hover {
	background: transparent;
	color: #66ccff;
}

.custom-navbar .navbar-nav > li > a:hover,
.custom-navbar .navbar-nav > li > a:focus {
	background-color: transparent;
}

.custom-navbar .nav li.active > a {
	background-color: transparent;
	color: #66ccff;
}

.custom-navbar .navbar-toggle {
	border: none;
	padding-top: 10px;
}

.custom-navbar .navbar-toggle {
	background-color: transparent;
}

.custom-navbar .navbar-toggle .icon-bar {
	background: #ffffff;
	border-color: transparent;
}

@media (min-width: 768px) {
	.custom-navbar {
		padding: 20px 0;
		border-bottom: 0;
		background: 0 0;
	}
	.custom-navbar.top-nav-collapse {
		background: #101010;
		padding: 0;
	}
}

/*---------------------------------------
    Intro section              
-----------------------------------------*/
#intro {
	-webkit-background-size: cover;
	background-size: cover;
	background-position: center center;
	color: #ffffff;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-webkit-align-items: center;
	-ms-flex-align: center;
	align-items: center;
	height: 100vh;
	text-align: center;
}

#intro h3 {
	letter-spacing: 2px;
}

/*---------------------------------------
   Overview  section              
-----------------------------------------*/
#overview img {
	position: relative;
	padding-bottom: 32px;
}

/*---------------------------------------
    Detail section              
-----------------------------------------*/
#detail {
	background: #222;
	color: #ffffff;
}

#detail .fa {
	color: #f2545f;
	font-size: 48px;
}

/*---------------------------------------
    Video section              
-----------------------------------------*/
#video iframe {
	border: none;
}

/*---------------------------------------
   Speakers section              
-----------------------------------------*/
#speakers {
	background: #f9f9f9;
}

#speakers h3 {
	font-size: 18px;
	margin-bottom: 0px;
}

#speakers h6 {
	color: #666;
	margin-top: 4px;
}

#speakers img {
	width: 100%;
	max-width: 100%;
	height: auto;
}

#speakers .col-md-3 {
	display: block;
	width: 100%;
	padding-top: 30px;
	padding-bottom: 20px;
	margin-bottom: 22px;
}

#speakers .speakers-wrapper {
	background: #ffffff;
	padding-bottom: 22px;
}

/*---------------------------------------
   Program section              
-----------------------------------------*/
#program h4 {
	color: #808080;
	font-size: 12px;
}

#program h3 {
	margin-top: 16px;
}

#program .program-divider {
	border: 1px solid #f9f9f9;
	margin-top: 32px;
	margin-bottom: 42px;
}

#program span {
	padding-right: 12px;
}

#program img {
	border-radius: 50%;
}

.nav-tabs {
	margin-bottom: 20px;
}
.nav-tabs > li {
	float: left;
	margin-bottom: -1px;
}
.nav-tabs > li > a {
	color: #000000;
	font-weight: 600;
	margin-right: 2px;
	line-height: 1.42857143;
	border: none;
	border-radius: 0px;
}
.nav-tabs > li > a:hover {
	background-color: transparent;
}
.nav-tabs > li.active > a,
.nav-tabs > li.active > a:hover,
.nav-tabs > li.active > a:focus {
	color: #f2545f;
	cursor: default;
	background-color: transparent;
	border: 1px solid transparent;
	border-bottom-color: #999;
}
.tab-content {
	padding-top: 20px;
}

/*---------------------------------------
    Regsiter section              
-----------------------------------------*/
#register {
	-webkit-background-size: cover;
	background-size: cover;
	background-position: center center;
	color: #ffffff;
}

#register .form-control {
	background: transparent;
	border: 2px solid #ffffff;
	border-radius: 0px;
	color: #ffffff;
	margin-bottom: 16px;
}

#register input {
	height: 45px;
}

#register input[type='submit'] {
	background: #f2545f;
	border-radius: 100px;
	border: none;
	color: #ffffff;
	letter-spacing: 2px;
	height: 50px;
	margin-top: 12px;
	transition: all 0.4s ease-in-out;
}

#register input[type='submit']:hover {
	background: #ffffff;
	color: #222;
}

/*---------------------------------------
   Faq section              
-----------------------------------------*/
#faq {
	background: #f9f9f9;
	padding-top: 8rem;
	padding-bottom: 8rem;
}

#faq .panel-group {
	margin-top: 32px;
}

#faq .panel-group .panel {
	margin-bottom: 8px;
}

#faq .panel-default {
	border: none;
}

#faq .panel-default > .panel-heading {
	background: #f0f0f0;
	color: #505050;
}

#faq .panel-heading {
	border: none;
	padding-top: 8px;
	padding-bottom: 6px;
}

#faq .panel-default > .panel-heading + .panel-collapse > .panel-body {
	border-top: none;
	padding-top: 22px;
	padding-left: 32px;
	padding-bottom: 22px;
}

#faq .panel-heading .panel-title a[data-toggle='collapse'] {
	font-size: 18px;
	font-weight: 500;
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	padding: 10px 15px;
}

#faq .panel-heading .panel-title a[data-toggle='collapse']::after {
	content: '-';
	display: block;
	position: absolute;
	line-height: 1;
	right: 1em;
	top: 50%;
	transform: translateY(-50%);
}

#faq .panel-heading .panel-title a[data-toggle='collapse'].collapsed::after {
	content: '+';
}

/*---------------------------------------
   Venue section              
-----------------------------------------*/
#venue {
	background-size: cover;
	background-position: center center;
	padding-top: 7rem;
	padding-bottom: 7rem;
}

#venue p {
	padding-top: 12px;
	padding-bottom: 18px;
}

/*---------------------------------------
   Sponsors section              
-----------------------------------------*/
#sponsors img {
	margin-top: 18px;
}

/*---------------------------------------
   Contact section              
-----------------------------------------*/
#contact {
	background-size: cover;
	background-position: center center;
}

#contact .contact_des {
	padding-top: 3rem;
	padding-right: 2rem;
}

#contact .contact_detail {
	background: #ffffff;
	padding: 5rem 5rem 12rem 5rem;
}

#contact .form-control {
	background: transparent;
	border: none;
	border-bottom: 1px solid #f0f0f0;
	border-radius: 0px;
	box-shadow: none;
	margin-bottom: 16px;
	transition: all 0.4s ease-in-out;
}

#contact .form-control:focus {
	border-bottom-color: #999;
}

#contact input {
	height: 45px;
}

#contact input[type='submit'] {
	background: #222;
	border-radius: 100px;
	border: none;
	color: #ffffff;
	letter-spacing: 2px;
	height: 50px;
	margin-top: 12px;
	transition: all 0.4s ease-in-out;
}

#contact input[type='submit']:hover {
	background: #f2545f;
}

/*---------------------------------------
   Footer section              
-----------------------------------------*/
footer {
	background-attachment: fixed;
}

/* Back top */
.go-top {
	background-color: #222;
	bottom: 2em;
	right: 2em;
	color: #ffffff;
	font-size: 32px;
	display: none;
	position: fixed;
	text-decoration: none;
	width: 60px;
	height: 60px;
	line-height: 60px;
	text-align: center;
	transition: all 0.4s ease-in-out;
}

.go-top:hover {
	background: #f2545f;
	color: #ffffff;
}

/*---------------------------------------
   Social icon             
-----------------------------------------*/
.social-icon {
	position: relative;
	padding: 0;
	margin: 0;
}

.social-icon li {
	display: inline-block;
	list-style: none;
}

.social-icon li a {
	color: #666;
	border-radius: 100px;
	font-size: 16px;
	text-decoration: none;
	transition: all 0.4s ease-in-out;
	width: 50px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	vertical-align: middle;
}

.social-icon li a:hover {
	background: #f2545f;
	color: #ffffff;
}

/*---------------------------------------
   Responsive styles            
-----------------------------------------*/
@media (max-width: 980px) {
	p {
		font-size: 13px;
	}

	.custom-navbar .navbar-brand {
		font-size: 2rem;
	}

	.custom-navbar .nav li a {
		font-size: 11px;
		line-height: 20px;
	}

	#intro {
		height: 65vh;
	}

	#video iframe {
		margin-top: 42px;
	}

	#contact .contact_detail {
		background: #ffffff;
		padding: 10rem 3rem 10rem 5rem;
	}
}

@media (max-width: 768px) {
	.custom-navbar .navbar-brand {
		line-height: 20px;
	}

	#overview img,
	#detail .col-md-4,
	#register form {
		padding-top: 32px;
	}

	.nav-tabs > li > a {
		font-size: 13px;
	}

	#program img {
		padding-bottom: 32px;
	}

	#sponsors img {
		padding-top: 22px;
	}

	#contact .contact_detail {
		margin-top: 82px;
	}
}

@media (max-width: 760px) {
	#intro {
		height: 100vh;
	}
}
